
.container {
  position: relative;
}

.page-container {
  padding: 10px;
}

.page-enter,
.page-exit {
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  overflow: hidden;
}

.page-enter {
  transform: translate(100%, 0);
}

.page-enter-active {
  transform: translate(0);
  transition: linear transform 400ms;
}

.page-exit {
  transform: translate(0);
}

.page-exit-active {
  transform: translate(-100%, 0);
  transition: linear transform 400ms;
}

p {
  font-size: 2em;
  margin: 10px 0;
}

